import './searchBar.css'
import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getWinesByName, getWines, changeSearchWine, cleanStateByName } from '../../redux/actions'

export const SearchBar = () => {
  const dispatch = useDispatch()
  const wines = useSelector(state => state.wines)

  const [input, setInput] = useState('')

  const handleInputChange = e => {
    setInput(e.target.value)
  }

  const handleSubmit = e => {
    e.preventDefault()
    setInput('')
    if (input) {
      // sino despacharia la accion de busqueda sin valor
      dispatch(getWinesByName(input))
      dispatch(changeSearchWine(true))
      dispatch(cleanStateByName([]))
      // cleanFilters()
    }
  }

  const handleClick = () => {
    dispatch(cleanStateByName([]))
    dispatch(changeSearchWine(false))
  }

  return (
    <div className="align-items-center">
      <form onSubmit={handleSubmit} className="d-flex" role="search">
        <input
          className="form-control"
          type="search"
          placeholder="Buscar"
          aria-label="Buscar"
          value={input}
          onChange={handleInputChange}
          list="datalistOptions"
        />
        {/* <button className="btn btn-secondary" type="button" onClick={handleFilterClear}>
          Limpiar filtro
        </button> */}
        <datalist id="datalistOptions">
          {wines.map(w => (
            <option key={w.id} value={w.name}>
              {w.name}
            </option>
          ))}
        </datalist>
      </form>
    </div>
  )
}
